
import XButton from 'vux/src/components/x-button/index.vue'

import { canJoin, lotteryJoin } from '@/api/'
export default {
data () {
return {
lotteryState: 0,
isJoin: false,
joinLoading: false,
joinTimer: null
}
},
created () {
this.loopCanJoin(0)
},
methods: {
loopCanJoin (time) {
if (!this.$route.query.id) {
clearTimeout(this.joinTimer)
this.joinTimer = null
return false
}
this.joinTimer = setTimeout(() => {
canJoin({ht_id: this.$route.query.id}).then((res) => {
console.log(res)
if (res.result === '活动已结束') {
this.lotteryState = 0
} else if (res.result === '允许报名') {
this.lotteryState = 1
} else if (res.result === '您已报过名') {
this.lotteryState = 1
this.isJoin = true
}
}).finally(() => {
if (!this.deleteTimer) {
return false
}
clearTimeout(this.deleteTimer)
this.loopCanJoin(2000)
})
}, time)
},
join () {
this.joinLoading = true
lotteryJoin({ht_id: this.$route.query.id}).then((res) => {
this.isJoin = true
}).finally(() => {
this.joinLoading = false
})
},
backTobar () {
var count = ~~(window.sessionStorage.getItem('count'))
if (count === 1) {
this.$router.replace(`/Main/${this.$route.query.id}`)
} else {
this.$router.go(-1)
}
}
},
components: {
XButton
}
}



